import React from 'react'
import Carousel from 'react-bootstrap/Carousel';
import Crow from '../images/crow.png';
import Nun from '../images/nun.png';

console.log(Crow);

function HomePage(props){

    return(
        <Carousel>
            <Carousel.Item>
                <img 
                //className="d-block w-100" 
                src={Crow}
                alt="First slide" 
                width="50%" height="50%"/>
                <Carousel.Caption>
                    <h3 color="">HomePage</h3>
                    <p>I made something.</p>
                </Carousel.Caption>
            </Carousel.Item>

            <Carousel.Item>
                <img
                //className="d-block w-100"
                src={Nun} 
                alt="Second slide"
                width="50%" height="50%"/>
                <Carousel.Caption>
                    <h3>Hi.</h3>
                    <p>I made something else.</p>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
    );
}

export default HomePage;