import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";
import './App.css';
import React from 'react';
import Container from 'react-bootstrap/esm/Container';
import Nav from 'react-bootstrap/esm/Nav';
import Navbar from 'react-bootstrap/esm/Navbar';

import 'bootstrap/dist/css/bootstrap.min.css';
import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';

//npm run start : localhost:3000
class App extends React.Component{ //Change function to class
  //Nav bar
  constructor(props){
    super(props);
    this.state={
      title: "Amanda Dieuaide",
      headerLinks: [
        {title: 'Home', path:'/'},
        {title: 'About', path:'/about'},
        {title: 'Contact', path:'/contact'}
      ],
      home: {
        title: 'Inspirational quote is inspirational',
        subTitle: "I'm trying so hard, but lose it all",
        text : "In the end, it doesn't even matter"
      },
      about: {
        title: "I am a potato"
      },
      contact: {
        title: "Call me maybe ;D"
      }

    }
  }
  
  
  render(){ //Add render and link to CSS
    return (
      <Router> 
        <Container className="p-0" fluid = {true}> 
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>Amanda Dieuaide</Navbar.Brand>
            <Navbar.Toggle className="ml-0" aria-controls="navbar-toggle"></Navbar.Toggle>
              <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link ml-auto" to="/">Home</Link>
                <Link className="nav-link ml-auto" to="/about">About</Link>
                <Link className="nav-link ml-auto" to="/contact">Contact</Link>
              </Nav>
              </Navbar.Collapse>
          </Navbar>

        <body class="row text-center">
        <Route path='/' exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} />
        <Route path='/about' exact render={() => <AboutPage title={this.state.about.title} subTitle={this.state.about.subTitle} text={this.state.about.text} />} />
        </body>
        <Footer></Footer>

        </Container>
      </Router>
    );
  }
  //Route path : Use react router to say that we want a certain path to render/display a certain component, use the link instead of anchor tag
  //exact : tells you that this it has to use the EXACT path and not only '/'
  //use this.state to pass data to the pages
}

export default App;
